import React, { Component } from 'react';
import {PieChart, Pie, Cell, ResponsiveContainer, Legend, Line, Tooltip } from 'recharts';

class Event extends Component {
    constructor(props) {
        super(props);
        this.state = {
            events: [],
            showDetails: false
        }
    }
    detailShow() {
        this.setState({ showDetails: true })
    }
    detailHide() {
        this.setState({ showDetails:false })
    }
    getData = () => {
        const rsvped = this.props.event.yes_rsvp_count;
        const max = this.props.event.rsvp_limit;
        const openings = max - this.props.event.yes_rsvp_count
    }
    render(){
        const { event } = this.props;
        let colors = ['#8884d8', '#82ca9d']

        return(
            <div className="event">
                <h2>{this.props.event.name}</h2>
                <button className="details" onClick = {() => this.detailShow()}>Show Details</button>
                {this.state.showDetails && (
                    <div className="the-details">{this.props.event.local_date}
                    <button className="noDetails" onClick = {() => this.detailHide()}>Hide</button>
                    {event.yes_rsvp_count && event.rsvp_limit ?
                    <ResponsiveContainer height={200}>

                        <PieChart width={200} height={200}>
                          <Pie data={this.getData()} dataKey="value" nameKey="name" cx="50%" cy="50%" innerRadius={60} outerRadius={80} label>
                            {
                              this.getData().map((entry, index) => (
                              <Cell key={`cell-${index}`} fill={colors[index]}/>
                              ))
                            }
                          </Pie>
                        
                          <Tooltip />
                        
                          <Legend verticalAlign="top" height={36}>
                          <Line name="Attending" type="monotone" dataKey="rsvped" stroke="#8884d8" />
                          <Line name="Spots Open" type="monotone" dataKey="openings" stroke="#82ca9d" />
                          </Legend>
                        
                        </PieChart>
                        
                        </ResponsiveContainer>
                        : null}
                    </div>
                )}
            </div>
        )
    }
}
export default Event;
